* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-family: 'Noto Sans KR', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.CoInfo {
    max-width: 1200px;
    margin: 0 auto;
}
.CoInfo .inner {
    font-size: 0;
}
.Chart {
    display: inline-block;
    width: 700px;
    box-sizing: border-box;
}
.List {
    display: inline-block;
    width: 500px;
    box-sizing: border-box;
}
.coin_list_inner {
    margin-top: 30px;
}
.coin_list_inner .c_list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-row-gap: 20px;
    padding: 15px;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
}
.coin_list_inner .c_list:hover {
    background: #f1f1f1;
}
.coin_list_inner > div {
    font-size: 16px;
}
.coin_list_inner .korean_name {
    color: #222;
    font-weight: 500;
}
.coin_list_inner .market_name {
    font-size: 10px;
    color: #777;
    line-height: 100%;
}
.coin_list_inner .tick_price {
    vertical-align: middle;
}
.coin_list_inner .tick {
    text-align: right;
}
.coin_list_inner .change {
    text-align: right;
}
.coin_list_inner .change_price {
    vertical-align: middle;
}
.coin_list_inner .change_price.RISE {
    color: #f75467;
}
.coin_list_inner .change_price.FALL {
    color: #4386f9;
}
.type .type_list {
    width: 25%;
    display: inline-block;
    line-height: 40px;
}
.type .type_list.on {
    border-bottom: 3px solid blue;
}
.type .type_list.on h3 {
    color: blue;
    font-weight: 500;
    opacity: 1;
}
.type h3 {
    font-size: 14px;
    font-weight: 400;
    color: #222;
    margin: 0;
    text-align: center;
    cursor: pointer;
}
.type h3:hover {
    color: blue;
    opacity: 0.5;
}
.App {
  font-size: 14px;
}


